<style lang="scss">
  .side-nav {
    width: 100%;
    box-sizing: border-box;
    padding-right: 30px;
    transition: opacity .3s;
    &.is-fade {
      transition: opacity 3s;
    }

    .side-search {
      position: absolute;
      width: calc(100% - 20px);
      z-index: 2;
      padding-top: 10px;
    }

    .search-list{
      .search-item{
        a {
          font-size: 14px;
          color:#333;
          line-height: 34px;
          height: 34px;
          margin: 0;
          padding: 0;
          text-decoration: none;
          display: block;
          position: relative;
          transition: .15s ease-out;

          &.active {
            color: #1890FF;
          }
        }
      }
    }

    .side-search ~ .side-body{
      padding-top:50px
    }

    li {
      list-style: none;
    }

    ul {
      padding: 0;
      margin: 0;
      overflow: hidden;
    }
    
    > ul > .nav-item > a {
      margin-top: 15px;
    }

    > ul > .nav-item:nth-child(-n + 4) > a {
      margin-top: 0;
    }

    .nav-item {
      a {
        font-size: 16px;
        color: #333;
        line-height: 40px;
        height: 40px;
        margin: 0;
        padding: 0;
        text-decoration: none;
        display: block;
        position: relative;
        transition: .15s ease-out;
        font-weight: bold;

        &.active {
          color: #1890FF;
        }
      }

      .nav-item {
        a {
          display: block;
          height: 40px;
          color: #444;
          line-height: 40px;
          font-size: 14px;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
          font-weight: normal;

          &:hover,
          &.active {
            color: #1890FF;
          }
        }
      }
      .sub-nav{
        a{
          font-size: 18px
        }
      }
      
    }

    .nav-group__title {
      font-size: 18px;
      margin: 5px 0;
      color: #444;
      & ~ .pure-menu-list{
        padding-left: 20px;
      }
    }

    #code-sponsor-widget {
      margin: 0 0 0 -20px;
    }
  }
  .nav-dropdown-list {
    width: 120px;
    margin-top: -8px;
    li {
      font-size: 14px;
    }
  }
</style>
<template>
  <div
    class="side-nav"
    @mouseenter="isFade = false"
    :class="{ 'is-fade': isFade }"
    :style="navStyle">
    <div class="side-search" v-if="$route.matched[0].path === '/component'">
      <el-input type="text" clearable placeholder="" v-model="search" />
    </div>
    <ul class="search-list side-body" v-if="search">
      <li class="search-item" v-for="item in searchMenu" :key="item.path">
        <router-link
          v-if="item.path"
          active-class="active"
          :to="base + item.path"
          exact
          v-text="item.title || item.name">
        </router-link>
      </li>
    </ul>
    <ul class="side-body" v-else>
      <li
        class="nav-item"
        v-for="(item, key) in data"
        :key="key">
        <router-link
          v-if="item.path"
          active-class="active"
          :to="base + item.path"
          exact
          v-text="item.title || item.name">
        </router-link>
        <div
          class="nav-group"
          v-for="(group, key) in item.groups"
          :key="key"
          >
          <div class="nav-group__title" @click="expandMenu">{{group.groupName}}</div>
          <ul class="pure-menu-list">
            <li
              class="nav-item"
              v-for="(navItem, key) in group.list"
              v-show="!navItem.disabled"
              :key="key">
              <router-link
                active-class="active"
                :to="base + navItem.path"
                exact
                v-text="navItem.title"></router-link>
            </li>
          </ul>
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
  import bus from '../bus';
  import compoLang from '../i18n/component.json';

  export default {
    props: {
      data: Array,
      base: {
        type: String,
        default: ''
      }
    },
    data() {
      return {
        search: '',
        highlights: [],
        navState: [],
        isSmallScreen: false,
        isFade: false
      };
    },
    watch: {
      '$route.path'() {
        this.handlePathChange();
      },
      isFade(val) {
        bus.$emit('navFade', val);
      }
    },
    computed: {
      navStyle() {
        const style = {};
        if (this.isSmallScreen) {
          style.paddingBottom = '60px';
        }
        style.opacity = this.isFade ? '0.5' : '1';
        return style;
      },
      lang() {
        return 'zh-CN';
      },
      langConfig() {
        return compoLang.filter(config => config.lang === this.lang)[0]['nav'];
      },
      menuItems() {
        return this.data[1].groups.map(group => {
          const components = group.list.filter((component) =>
            !this.search.trim() ||
            component.title.toLowerCase().indexOf(this.search.trim().toLowerCase()) > -1 ||
            (component.title || '').toLowerCase().indexOf(this.search.trim().toLowerCase()) > -1
          );
          return components;
        });
      },
      searchMenu() {
        const deepFlatten = (arr) => {
          return [].concat(...arr.map(v => (Array.isArray(v) ? deepFlatten(v) : v)));
        };
        return deepFlatten(this.menuItems);
      }
    },
    methods: {
      handleResize() {
        this.isSmallScreen = document.documentElement.clientWidth < 768;
        this.handlePathChange();
      },
      handlePathChange() {
        if (!this.isSmallScreen) {
          this.expandAllMenu();
          return;
        }
        this.$nextTick(() => {
          this.hideAllMenu();
          let activeAnchor = this.$el.querySelector('a.active');
          let ul = activeAnchor.parentNode;
          while (ul.tagName !== 'UL') {
            ul = ul.parentNode;
          }
          ul.style.height = 'auto';
        });
      },
      hideAllMenu() {
        [].forEach.call(this.$el.querySelectorAll('.pure-menu-list'), ul => {
          ul.style.height = '0';
        });
      },
      expandAllMenu() {
        [].forEach.call(this.$el.querySelectorAll('.pure-menu-list'), ul => {
          ul.style.height = 'auto';
        });
      },
      expandMenu(event) {
        if (!this.isSmallScreen) return;
        let target = event.currentTarget;
        if (!target.nextElementSibling || target.nextElementSibling.tagName !== 'UL') return;
        this.hideAllMenu();
        event.currentTarget.nextElementSibling.style.height = 'auto';
      }
    },
    created() {
      bus.$on('fadeNav', () => {
        this.isFade = true;
      });
    },
    mounted() {
      this.handleResize();
      window.addEventListener('resize', this.handleResize);
    },
    beforeDestroy() {
      window.removeEventListener('resize', this.handleResize);
    }
  };
</script>
