import { arrayFindIndex, noop } from 'element-gui/src/utils/util';
import { getCell, getColumnByCell, getRowIdentity } from './util';
import { getStyle, hasClass, removeClass, addClass } from 'element-gui/src/utils/dom';
import ElCheckbox from 'element-gui/packages/checkbox';
import ElTooltip from 'element-gui/packages/tooltip';
import debounce from 'throttle-debounce/debounce';
import LayoutObserver from './layout-observer';
import { mapStates } from './store/helper';
import TableRow from './table-row.js';
import Vue from 'vue';

export default {
  name: 'ElTableBody',

  mixins: [LayoutObserver],

  components: {
    ElCheckbox,
    ElTooltip,
    TableRow
  },

  props: {
    store: {
      required: true
    },
    name: String,
    stripe: Boolean,
    context: {},
    rowClassName: [String, Function],
    rowStyle: [Object, Function],
    fixed: String,
    highlight: Boolean,
    noHover: Boolean
  },

  render(h) {
    const data = this.data || [];
    return (
      <table
        class="el-table__body"
        cellspacing="0"
        cellpadding="0"
        border="0">
        <colgroup>
          {
            this.columns.map(column => <col name={column.id} key={column.id} />)
          }
        </colgroup>
        <tbody>
          {
            data.reduce((acc, row) => {
              const isSelected = this.store.isSelected(row);
              const isExpanded = this.store.states.expandRows.indexOf(row) > -1;
              return acc.concat(this.wrappedRowRender({
                row,
                $index: acc.length,
                isSelected,
                isExpanded
              }));
            }, [])
          }
        </tbody>
      </table>
    );
  },

  computed: {
    table() {
      let el = null;
      if (this.$parent.table || this.$parent.layout) {
        el = this.$parent;
      } else {
        el = this.$parent.$parent;
      }
      return el;
    },

    ...mapStates({
      data: 'data',
      columns: 'columns',
      treeIndent: 'indent',
      leftFixedLeafCount: 'fixedLeafColumnsLength',
      rightFixedLeafCount: 'rightFixedLeafColumnsLength',
      columnsCount: states => states.columns.length,
      leftFixedCount: states => states.fixedColumns.length,
      rightFixedCount: states => states.rightFixedColumns.length,
      hasExpandColumn: states => states.columns.some(({ type }) => type === 'expand')
    }),

    columnsHidden() {
      return this.columns.map((column, index) => this.isColumnHidden(index));
    },

    firstDefaultColumnIndex() {
      return arrayFindIndex(this.columns, ({ type }) => type === 'default');
    }
  },

  watch: {
    // don't trigger getter of currentRow in getCellClass. see https://jsfiddle.net/oe2b4hqt/
    // update DOM manually. see https://github.com/ElemeFE/element/pull/13954/files#diff-9b450c00d0a9dec0ffad5a3176972e40
    'store.states.hoverRow'(newVal, oldVal) {
      if (!this.store.states.isComplex || this.$isServer) return;
      let raf = window.requestAnimationFrame;
      if (!raf) {
        raf = (fn) => setTimeout(fn, 16);
      }
      raf(() => {
        const rows = this.$el.querySelectorAll('.el-table__row');
        const oldRow = rows[oldVal];
        const newRow = rows[newVal];
        if (oldRow) {
          removeClass(oldRow, 'hover-row');
        }
        if (newRow) {
          addClass(newRow, 'hover-row');
        }
      });
    }
  },

  data() {
    return {
      tooltipContent: ''
    };
  },

  created() {
    this.createPopper();
    this.activateTooltip = debounce(50, tooltip => tooltip.handleShowPopper());
  },

  methods: {
    getMergeColumnsMethod() {
      if (this.table.mergeColumns == null) {
        return null;
      }
      let mergeRowMap = {};
      this.table.mergeColumns.forEach(item => {
        mergeRowMap[item.key] = item.conditions;
      });

      function isSimilar(data1, data2, keyList) {
        for (let key of keyList) {
          if (data1[key] !== data2[key]) {
            return false;
          }
        }
        return true;
      }
      return ({
        row,
        column,
        rowIndex
      }) => {
        let mergeConditions = mergeRowMap[column.property];
        if (mergeConditions == null || !Array.isArray(mergeConditions)) {
          return [1, 1];
        }
        if (rowIndex !== 0 && isSimilar(row, this.data[rowIndex - 1], mergeConditions)) {
          return [0, 0];
        } else {
          let i = 1;
          for (; i < this.data.length - rowIndex; i++) {
            if (!isSimilar(row, this.data[rowIndex + i], mergeConditions)) {
              break;
            }
          }
          return [i, 1];
        }
      };
    },

    getKeyOfRow(row, index) {
      const rowKey = this.table.rowKey;
      if (rowKey) {
        return getRowIdentity(row, rowKey);
      }
      return index;
    },

    isColumnHidden(index) {
      if (this.fixed === true || this.fixed === 'left') {
        return index >= this.leftFixedLeafCount;
      } else if (this.fixed === 'right') {
        return index < this.columnsCount - this.rightFixedLeafCount;
      } else {
        return (index < this.leftFixedLeafCount) || (index >= this.columnsCount - this.rightFixedLeafCount);
      }
    },

    getSpan(row, column, rowIndex, columnIndex) {
      let rowspan = 1;
      let colspan = 1;
      const fn = this.table.spanMethod || this.getMergeColumnsMethod();
      if (typeof fn === 'function') {
        const result = fn({
          row,
          column,
          rowIndex,
          columnIndex
        });
        if (Array.isArray(result)) {
          rowspan = result[0];
          colspan = result[1];
        } else if (typeof result === 'object') {
          rowspan = result.rowspan;
          colspan = result.colspan;
        }
      }
      return { rowspan, colspan };
    },

    getRowStyle(row, rowIndex) {
      const rowStyle = this.table.rowStyle;
      if (typeof rowStyle === 'function') {
        return rowStyle.call(null, {
          row,
          rowIndex
        });
      }
      return rowStyle || null;
    },

    getRowClass(row, rowIndex) {
      const classes = ['el-table__row'];
      if (this.table.highlightCurrentRow && row === this.store.states.currentRow) {
        classes.push('current-row');
      }

      if (this.stripe && rowIndex % 2 === 1) {
        classes.push('el-table__row--striped');
      }
      const rowClassName = this.table.rowClassName;
      if (typeof rowClassName === 'string') {
        classes.push(rowClassName);
      } else if (typeof rowClassName === 'function') {
        classes.push(rowClassName.call(null, {
          row,
          rowIndex
        }));
      }

      if (this.store.states.expandRows.indexOf(row) > -1) {
        classes.push('expanded');
      }

      return classes;
    },

    getCellStyle(rowIndex, columnIndex, row, column) {
      const cellStyle = this.table.cellStyle;
      if (typeof cellStyle === 'function') {
        return cellStyle.call(null, {
          rowIndex,
          columnIndex,
          row,
          column
        });
      }
      return cellStyle;
    },

    getCellClass(rowIndex, columnIndex, row, column) {
      const classes = [column.id, column.align, column.className];

      if (this.isColumnHidden(columnIndex)) {
        classes.push('is-hidden');
      }

      const cellClassName = this.table.cellClassName;
      if (typeof cellClassName === 'string') {
        classes.push(cellClassName);
      } else if (typeof cellClassName === 'function') {
        classes.push(cellClassName.call(null, {
          rowIndex,
          columnIndex,
          row,
          column
        }));
      }

      classes.push('el-table__column');

      return classes.join(' ');
    },

    getCellId(rowIndex, columnIndex) {
      let name = this.name || 'table';
      let id = name + '-row' + '_' + rowIndex + '_column_' + columnIndex;
      return id;
    },

    getColspanRealWidth(columns, colspan, index) {
      if (colspan < 1) {
        return columns[index].realWidth;
      }
      const widthArr = columns.map(({ realWidth }) => realWidth).slice(index, index + colspan);
      return widthArr.reduce((acc, width) => acc + width, -1);
    },

    handleCellMouseEnter(event, row) {
      const table = this.table;
      const cell = getCell(event);

      if (cell) {
        const column = getColumnByCell(table, cell);
        const hoverState = table.hoverState = { cell, column, row };
        table.$emit('cell-mouse-enter', hoverState.row, hoverState.column, hoverState.cell, event);
      }

      // text-overflow, tooltip
      const cellChild = event.target.querySelector('.cell');
      if (!(hasClass(cellChild, 'el-tooltip__cell') && cellChild.childNodes.length)) {
        return;
      }
      // use range width instead of scrollWidth to determine whether the text is overflowing
      // to address a potential FireFox bug: https://bugzilla.mozilla.org/show_bug.cgi?id=1074543#c3
      const range = document.createRange();
      range.setStart(cellChild, 0);
      range.setEnd(cellChild, cellChild.childNodes.length);
      const rangeWidth = range.getBoundingClientRect().width;
      const padding = (parseInt(getStyle(cellChild, 'paddingLeft'), 10) || 0) +
        (parseInt(getStyle(cellChild, 'paddingRight'), 10) || 0);
      if (rangeWidth + padding > cellChild.offsetWidth || cellChild.scrollWidth > cellChild.offsetWidth) {
        const tooltip = this._tooltip;
        // TODO  Table 
        tooltip.content = cell.innerText || cell.textContent;
        tooltip.referenceElm = cell;
        tooltip.$refs.popper && (tooltip.$refs.popper.style.display = 'none');
        tooltip.doDestroy();
        tooltip.setExpectedState(true);
        this.activateTooltip(tooltip);
      }
    },

    handleCellMouseLeave(event) {
      const tooltip = this._tooltip;
      if (tooltip) {
        tooltip.setExpectedState(false);
        tooltip.handleClosePopper();
      }
      const cell = getCell(event);
      if (!cell) return;

      const oldHoverState = this.table.hoverState || {};
      this.table.$emit('cell-mouse-leave', oldHoverState.row, oldHoverState.column, oldHoverState.cell, event);
    },

    handleMouseEnter: debounce(30, function(index) {
      this.store.commit('setHoverRow', index);
    }),

    handleMouseLeave: debounce(30, function() {
      this.store.commit('setHoverRow', null);
    }),

    handleContextMenu(event, row) {
      this.handleEvent(event, row, 'contextmenu');
    },

    handleDoubleClick(event, row) {
      this.handleEvent(event, row, 'dblclick');
    },

    handleClick(event, row) {
      this.store.commit('setCurrentRow', row);
      this.handleEvent(event, row, 'click');
    },

    handleEvent(event, row, name) {
      const table = this.table;
      const cell = getCell(event);
      let column;
      if (cell) {
        column = getColumnByCell(table, cell);
        if (column) {
          table.$emit(`cell-${name}`, row, column, cell, event);
        }
      }
      table.$emit(`row-${name}`, row, column, event);
    },

    rowRender({row, $index, treeRowData, isSelected, isExpanded}) {
      const { treeIndent, columns, firstDefaultColumnIndex } = this;
      const rowClasses = this.getRowClass(row, $index);
      let display = true;
      if (treeRowData) {
        rowClasses.push('el-table__row--level-' + treeRowData.level);
        display = treeRowData.display;
      }
      //  v-show  row-style  display
      //  :style  v-show https://github.com/ElemeFE/element/issues/16995
      let displayStyle = display ? null : {
        display: 'none'
      };
      return (
        <TableRow
          style={[displayStyle, this.getRowStyle(row, $index)]}
          class={rowClasses}
          key={this.getKeyOfRow(row, $index)}
          nativeOn-dblclick={($event) => this.handleDoubleClick($event, row)}
          nativeOn-click={($event) => this.handleClick($event, row)}
          nativeOn-contextmenu={($event) => this.handleContextMenu($event, row)}
          nativeOn-mouseenter={this.noHover ? noop : (_ => this.handleMouseEnter($index))}
          nativeOn-mouseleave={ this.noHover ? noop : this.handleMouseLeave }
          columns={columns}
          row={row}
          index={$index}
          store={this.store}
          context={this.context || this.table.$vnode.context}
          firstDefaultColumnIndex={firstDefaultColumnIndex}
          treeRowData={treeRowData}
          treeIndent={treeIndent}
          columnsHidden={this.columnsHidden}
          getSpan={this.getSpan}
          getColspanRealWidth={this.getColspanRealWidth}
          getCellStyle={this.getCellStyle}
          getCellClass={this.getCellClass}
          handleCellMouseEnter={this.handleCellMouseEnter}
          handleCellMouseLeave={this.handleCellMouseLeave}
          isSelected={isSelected}
          isExpanded={isExpanded}
          fixed={this.fixed}
        >
        </TableRow>
      );
      // return (<tr
      //   style={ [displayStyle, this.getRowStyle(row, $index)] }
      //   class={ rowClasses }
      //   key={ this.getKeyOfRow(row, $index) }
      //   on-dblclick={ ($event) => this.handleDoubleClick($event, row) }
      //   on-click={ ($event) => this.handleClick($event, row) }
      //   on-contextmenu={ ($event) => this.handleContextMenu($event, row) }
      //   on-mouseenter={ this.noHover ? noop : (_ => this.handleMouseEnter($index)) }
      //   on-mouseleave={ this.noHover ? noop : this.handleMouseLeave }>
      //   {
      //     columns.map((column, cellIndex) => {
      //       const { rowspan, colspan } = this.getSpan(row, column, $index, cellIndex);
      //       if (!rowspan || !colspan) {
      //         return null;
      //       }
      //       const columnData = { ...column };
      //       columnData.realWidth = this.getColspanRealWidth(columns, colspan, cellIndex);
      //       const data = {
      //         store: this.store,
      //         _self: this.context || this.table.$vnode.context,
      //         column: columnData,
      //         row,
      //         $index
      //       };
      //       if (cellIndex === firstDefaultColumnIndex && treeRowData) {
      //         data.treeNode = {
      //           indent: treeRowData.level * treeIndent,
      //           level: treeRowData.level
      //         };
      //         if (typeof treeRowData.expanded === 'boolean') {
      //           data.treeNode.expanded = treeRowData.expanded;
      //           // 
      //           if ('loading' in treeRowData) {
      //             data.treeNode.loading = treeRowData.loading;
      //           }
      //           if ('noLazyChildren' in treeRowData) {
      //             data.treeNode.noLazyChildren = treeRowData.noLazyChildren;
      //           }
      //         }
      //       }
      //       return (
      //         <td
      //           style={ this.getCellStyle($index, cellIndex, row, column) }
      //           class={['el-table__column' ,this.getCellClass($index, cellIndex, row, column)]}
      //           id={ this.getCellId($index, cellIndex)}
      //           rowspan={ rowspan }
      //           colspan={ colspan }
      //           on-mouseenter={ ($event) => this.handleCellMouseEnter($event, row) }
      //           on-mouseleave={ this.handleCellMouseLeave }>
      //           {
      //             column.renderCell.call(
      //               this._renderProxy,
      //               this.$createElement,
      //               data,
      //               columnsHidden[cellIndex]
      //             )
      //           }
      //         </td>
      //       );
      //     })
      //   }
      // </tr>);
    },

    wrappedRowRender({row, $index, isSelected, isExpanded}) {
      const store = this.store;
      const { isRowExpanded, assertRowKey } = store;
      const { treeData, lazyTreeNodeMap, childrenColumnName, rowKey } = store.states;
      if (this.hasExpandColumn && isRowExpanded(row)) {
        const renderExpanded = this.table.renderExpanded;
        const tr = this.rowRender({ row, $index, isSelected, isExpanded });
        if (!renderExpanded) {
          console.error('[Element Error]renderExpanded is required.');
          return tr;
        }
        //  $index
        return [[
          tr,
          <tr key={'expanded-row__' + tr.key}>
            <td colspan={ this.columnsCount } class="el-table__expanded-cell">
              { renderExpanded(this.$createElement, { row, $index, store: this.store }) }
            </td>
          </tr>]];
      } else if (Object.keys(treeData).length) {
        assertRowKey();
        // TreeTable rowKey  getKeyOfRow 
        //  rowRender  rowKey
        const key = getRowIdentity(row, rowKey);
        let cur = treeData[key];
        let treeRowData = null;
        if (cur) {
          treeRowData = {
            expanded: cur.expanded,
            level: cur.level,
            display: true
          };
          if (typeof cur.lazy === 'boolean') {
            if (typeof cur.loaded === 'boolean' && cur.loaded) {
              treeRowData.noLazyChildren = !(cur.children && cur.children.length);
            }
            treeRowData.loading = cur.loading;
          }
        }
        const tmp = [this.rowRender({ row, $index, treeRowData, isSelected, isExpanded })];
        // 
        if (cur) {
          // currentRow  index TreeTable  index
          let i = 0;
          const traverse = (children, parent) => {
            if (!(children && children.length && parent)) return;
            children.forEach(node => {
              //  display 
              const innerTreeRowData = {
                display: parent.display && parent.expanded,
                level: parent.level + 1
              };
              const childKey = getRowIdentity(node, rowKey);
              if (childKey === undefined || childKey === null) {
                throw new Error('for nested data item, row-key is required.');
              }
              cur = { ...treeData[childKey] };
              // 
              //  expanded 
              //  display  expanded  display 
              if (cur) {
                innerTreeRowData.expanded = cur.expanded;
                // level 
                cur.level = cur.level || innerTreeRowData.level;
                cur.display = !!(cur.expanded && innerTreeRowData.display);
                if (typeof cur.lazy === 'boolean') {
                  if (typeof cur.loaded === 'boolean' && cur.loaded) {
                    innerTreeRowData.noLazyChildren = !(cur.children && cur.children.length);
                  }
                  innerTreeRowData.loading = cur.loading;
                }
              }
              i++;
              tmp.push(this.rowRender({ row: node, $index: $index + i, treeRowData: innerTreeRowData, isSelected, isExpanded }));
              if (cur) {
                const nodes = lazyTreeNodeMap[childKey] || node[childrenColumnName];
                traverse(nodes, cur);
              }
            });
          };
          //  root display  true
          cur.display = true;
          const nodes = lazyTreeNodeMap[key] || row[childrenColumnName];
          traverse(nodes, cur);
        }
        return tmp;
      } else {
        return this.rowRender({ row, $index, isSelected, isExpanded });
      }
    },

    createPopper() {
      const TooltipChild = Vue.extend(ElTooltip);
      this._tooltip = new TooltipChild({
        propsData: {
          placement: 'top',
          effect: this.table.tooltipEffect,
          popperClass: 'table-tooltip'
        }
      });
      const Wapper = document.createElement('div');
      document.body.appendChild(Wapper);
      this._tooltip.$mount(Wapper);
    }
  },

  destroyed() {
    this._tooltip && this._tooltip.$el && document.body.removeChild(this._tooltip.$el);
  }
};
