<template>
  <div class="right-nav" v-if="anchors">
    <ul>
      <li :id="item" v-for="(item, i) in anchors" :key="'item' + i" class="right-nav-link" :class="active === item ? 'primary' : 'default'">
        <div class="line"></div>
        <em :title="item" class="link" @click="handleAnchorClick(item)">{{ item }}</em>
      </li>
    </ul>
  </div>
</template>

<script>
import ElScrollbar from 'element-gui/packages/scrollbar';
import ElLink from 'element-gui/packages/link';
import ResizeObserver from 'resize-observer-polyfill';
import { isIE } from 'element-gui/src/utils/util';
export default {
  components: {
    ElScrollbar,
    ElLink
  },
  mounted() {
    if (!Array.from) {
      Array.from = function(el) {
        return Array.apply(this, el);
      };
    };
    // iefindIndex
    /*eslint no-extend-native: ["error", { "exceptions": ["Array"] }]*/
    if (!Array.prototype.findIndex) {
      Object.defineProperty(Array.prototype, 'findIndex', {
        value: function(predicate) {
          // 1. Let O be ? ToObject(this value).
          if (this == null) {
            throw new TypeError('"this" is null or not defined');
          }
          var o = Object(this);
          // 2. Let len be ? ToLength(? Get(O, "length")).
          var len = o.length >>> 0;
          // 3. If IsCallable(predicate) is false, throw a TypeError exception.
          if (typeof predicate !== 'function') {
            throw new TypeError('predicate must be a function');
          }
          // 4. If thisArg was supplied, let T be thisArg; else let T be undefined.
          var thisArg = arguments[1];
          // 5. Let k be 0.
          var k = 0;
          // 6. Repeat, while k < len
          while (k < len) {
            // a. Let Pk be ! ToString(k).
            // b. Let kValue be ? Get(O, Pk).
            // c. Let testResult be ToBoolean(? Call(predicate, T,  kValue, k, O )).
            // d. If testResult is true, return k.
            var kValue = o[k];
            if (predicate.call(thisArg, kValue, k, o)) {
              return k;
            }
            // e. Increase k by 1.
            k++;
          }
          // 7. Return -1.
          return -1;
        }
      });
    }
    this.$nextTick(() => {
      const map = this.map;
      this.scrollContainer = document.querySelector(
        '.el-scrollbar.page-component__scroll>.el-scrollbar__wrap'
      );
      const content = document.querySelector('.element-doc');
      if (!content) return;
      const h3 = content.querySelectorAll('h3');
      this.anchors = Array.from(h3).map((item) => {
        const text = item.childNodes[1].textContent.trim();
        map.set(text, item.offsetTop);
        return text;
      });
      let mapValues = Array.from(map.values()).reverse();
      let mapKeys = Array.from(map.keys()).reverse();
      this.resizeObserver = new ResizeObserver(() => {
        Array.from(h3).forEach((item) => {
          const text = item.childNodes[1].textContent.trim();
          map.set(text, item.offsetTop);
        });
        mapValues = Array.from(map.values()).reverse();
        mapKeys = Array.from(map.keys()).reverse();
      });
      this.resizeObserver.observe(this.scrollContainer.childNodes[0]);
      let cachedIndex = -1;
      let that = this;
      this.scrollContainer.addEventListener('scroll', () => {
        const index = mapValues.findIndex(
          (item) => that.scrollContainer.scrollTop > item - 75
        );
        if (cachedIndex !== index && index !== -1) {
          that.active = mapKeys[index];
          cachedIndex = index;
          if (document.getElementById(that.active)) {
            document.getElementById(that.active).focus();
            document.getElementById(that.active).scrollIntoView(true);
          }
        }
      });
    });
  },
  data() {
    return {
      scrollContainer: null,
      anchors: [],
      active: '',
      resizeObserver: null
      /* global Map*/
      // map: new this.Map()
    };
  },
  computed: {
    map() {
      return isIE() ? new this.Map() : new Map();
    }
  },
  methods: {
    handleAnchorClick(anchor) {
      this.scrollContainer.scrollTop = this.map.get(anchor) - 10;
      this.active = anchor;
    },
    Map() {
      this.elements = [];
      // Map
      this.size = function() {
        return this.elements.length;
      };
      // Map
      this.isEmpty = function() {
        return (this.elements.length < 1);
      };
      // Map
      this.clear = function() {
        this.elements = [];
      };
      // Mapkey, value)
      this.put = function(_key, _value) {
        if (this.containsKey(_key) == true) {
          if (this.containsValue(_value)) {
            if (this.remove(_key) == true) {
              this.elements.push({
                key: _key,
                value: _value
              });
            }
          } else {
            this.elements.push({
              key: _key,
              value: _value
            });
          }
        } else {
          this.elements.push({
            key: _key,
            value: _value
          });
        }
      };
      // Mapkey, value)
      this.set = function(_key, _value) {
        if (this.containsKey(_key) == true) {
          if (this.containsValue(_value)) {
            if (this.remove(_key) == true) {
              this.elements.push({
                key: _key,
                value: _value
              });
            }
          } else {
            this.elements.push({
              key: _key,
              value: _value
            });
          }
        } else {
          this.elements.push({
            key: _key,
            value: _value
          });
        }
      };
      // keytruefalse
      this.remove = function(_key) {
        var bln = false;
        try {
          for (let i = 0; i < this.elements.length; i++) {
            if (this.elements[i].key == _key) {
              this.elements.splice(i, 1);
              return true;
            }
          }
        } catch (e) {
          bln = false;
        }
        return bln;
      };

      // keytruefalse
      this.delete = function(_key) {
        var bln = false;
        try {
          for (let i = 0; i < this.elements.length; i++) {
            if (this.elements[i].key == _key) {
              this.elements.splice(i, 1);
              return true;
            }
          }
        } catch (e) {
          bln = false;
        }
        return bln;
      };

      // keyvaluenull
      this.get = function(_key) {
        try {
          for (let i = 0; i < this.elements.length; i++) {
            if (this.elements[i].key == _key) {
              return this.elements[i].value;
            }
          }
        } catch (e) {
          return null;
        }
      };

      // setkeyvalue
      this.setValue = function(_key, _value) {
        var bln = false;
        try {
          for (let i = 0; i < this.elements.length; i++) {
            if (this.elements[i].key == _key) {
              this.elements[i].value = _value;
              return true;
            }
          }
        } catch (e) {
          bln = false;
        }
        return bln;
      };

      // element.keyelement.valuekeyvaluenull
      this.element = function(_index) {
        if (_index < 0 || _index >= this.elements.length) {
          return null;
        }
        return this.elements[_index];
      };

      // Mapkey
      this.containsKey = function(_key) {
        var bln = false;
        try {
          for (let i = 0; i < this.elements.length; i++) {
            if (this.elements[i].key == _key) {
              bln = true;
            }
          }
        } catch (e) {
          bln = false;
        }
        return bln;
      };

      // Mapkey
      this.has = function(_key) {
        var bln = false;
        try {
          for (let i = 0; i < this.elements.length; i++) {
            if (this.elements[i].key == _key) {
              bln = true;
            }
          }
        } catch (e) {
          bln = false;
        }
        return bln;
      };

      // Mapvalue
      this.containsValue = function(_value) {
        var bln = false;
        try {
          for (let i = 0; i < this.elements.length; i++) {
            if (this.elements[i].value == _value) {
              bln = true;
            }
          }
        } catch (e) {
          bln = false;
        }
        return bln;
      };

      // Mapkeyarray
      this.keys = function() {
        var arr = [];
        for (let i = 0; i < this.elements.length; i++) {
          arr.push(this.elements[i].key);
        }
        return arr;
      };

      // Mapvaluearray
      this.values = function() {
        var arr = [];
        for (let i = 0; i < this.elements.length; i++) {
          arr.push(this.elements[i].value);
        }
        return arr;
      };

      /**
       * map
       * @param callback [function] 
       * @param context [object] 
       */
      this.forEach = function forEach(callback, context) {
        context = context || window;
        // IE6-8
        var newAry = [];
        for (let i = 0; i < this.elements.length; i++) {
          if (typeof callback === 'function') {
            var val = callback.call(context, this.elements[i].value, this.elements[i].key, this.elements);
            newAry.push(this.elements[i].value);
          }
        }
        return newAry;
      };
    }
  }
};
</script>

<style lang="scss">
.right-nav {
  position: fixed;
  right: 10px;
  top: 70px;
  width: 210px;
  margin-left: 10px;
  height: calc(100% - 140px);
  overflow: hidden;
  ul {
    margin: 0;
    padding: 0;
    width: calc(100% + 17px);
    height: 100%;
    overflow-y: scroll;
    .right-nav-link:last-child{
      .line{
        border-left-color: transparent;
      }
    }
  }
  .right-nav-link {
    position: relative;
    list-style: none;
    line-height: 26px;
    .link:hover{
      color: #46a6ff
    }
    &.primary{
      .line{
        &::before{
          background-color: #46a6ff;
        }
      }
      .link{
        color: #46a6ff;
      }
    }
    .line{
      position: absolute;
      top: 13px;
      left: 4px;
      height: 100%;
      border-left: 1px solid #d2d2d2;
      &::before{
        content: '';
        position: absolute;
        left: -4px;
        top: -4px;
        background-color: #d2d2d2;
        width: 7px;
        height: 7px;
        border-radius: 50%;
      }
    }
    .link {
      display: inline-block;
      width: 190px;
      font-style: normal;
      margin-left: 15px;
      font-size: 12px;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
      cursor: pointer;
    }
  }
}
@media (max-width: 1490px) {
  .right-nav {
    display: none;
  }
  .page-component .page-component__content {
    padding-right: 20px !important;
  }
}
</style>