import navConfig from './nav.config';
import guideConfig from './guide.config';
import langs from './i18n/route';

const LOAD_MAP = {
  'zh-CN': name => {
    return r => require.ensure([], () =>
      r(require(`./pages/zh-CN/${name}.vue`)),
    'zh-CN');
  }
  // 'en-US': name => {
  //   return r => require.ensure([], () =>
  //     r(require(`./pages/en-US/${name}.vue`)),
  //   'en-US');
  // },
  // 'es': name => {
  //   return r => require.ensure([], () =>
  //     r(require(`./pages/es/${name}.vue`)),
  //   'es');
  // },
  // 'fr-FR': name => {
  //   return r => require.ensure([], () =>
  //     r(require(`./pages/fr-FR/${name}.vue`)),
  //   'fr-FR');
  // }
};

const load = function(lang, path) {
  return LOAD_MAP[lang](path);
};

const LOAD_DOCS_MAP = {
  // 
  'zh-CN': (module, path) => {
    return r => require.ensure([], () =>
      r(
        require(`./docs/zh-CN/${module}${path}.md`)
      ),
    'zh-CN');
  }
  // 'en-US': path => {
  //   return r => require.ensure([], () =>
  //     r(require(`./docs/en-US${path}.md`)),
  //   'en-US');
  // },
  // 'es': path => {
  //   return r => require.ensure([], () =>
  //     r(require(`./docs/es${path}.md`)),
  //   'es');
  // },
  // 'fr-FR': path => {
  //   return r => require.ensure([], () =>
  //     r(require(`./docs/fr-FR${path}.md`)),
  //   'fr-FR');
  // }
};

const loadDocs = function(lang, module, path) {
  return LOAD_DOCS_MAP[lang](module, path);
};

const registerRoute = (navConfig, module) => {
  let route = [];
  Object.keys(navConfig).forEach((lang, index) => {
    let navs = navConfig[lang];
    route.push({
      path: '/' + module,
      redirect: '/' + module + (module === 'guide' ? '/preface' : '/installation'),
      component: load(lang, module),
      children: []
    });

    navs.forEach(nav => {
      if (nav.href) return;
      if (nav.groups) {
        nav.groups.forEach(group => {
          group.list.forEach(nav => {
            addRoute(nav, lang, index);
          });
        });
      } else {
        addRoute(nav, lang, index);
      }
    });
  });
  function addRoute(page, lang, index) {
    const component = page.path === '/changelog'
      ? load(lang, 'changelog')
      : loadDocs(lang, module, page.path);
    let child = {
      path: page.path.slice(1),
      meta: {
        title: page.title || page.name,
        description: page.description,
        lang
      },
      name: module + '-' + lang + (page.title || page.name),
      component: component.default || component
    };

    route[index].children.push(child);
  }

  return route;
};

let route = [];

const generateMiscRoutes = function(lang) {
  let componentRoute = registerRoute(navConfig, 'component');
  let guideRoute = registerRoute(guideConfig, 'guide');
  let resourceRoute = {
    path: '/resource', // 
    meta: { lang },
    name: 'resource' + lang,
    component: load(lang, 'resource')
  };
  let indexRoute = {
    path: '/', // 
    meta: { lang },
    name: 'home',
    component: load(lang, 'index')
  };
  return [...componentRoute, ...guideRoute, resourceRoute, indexRoute];
};

langs.forEach(lang => {
  route = route.concat(generateMiscRoutes(lang.lang));
});

route.push({
  path: '/play',
  name: 'play',
  component: require('./play/index.vue')
});

let defaultPath = '/';
// if (userLanguage.indexOf('zh-') !== -1) {
//   defaultPath = '/';
// } else if (userLanguage.indexOf('es') !== -1) {
//   defaultPath = '/es';
// } else if (userLanguage.indexOf('fr') !== -1) {
//   defaultPath = '/fr-FR';
// }

route = route.concat([{
  path: '/',
  redirect: defaultPath
}, {
  path: '*',
  redirect: defaultPath
}]);

export default route;
